import Vue from 'vue'
import Router from 'vue-router'
// import DashBoard from '@/views/DashBoard'
import Login from '@/views/login/Login'
import LoginForm from '@/components/login/LoginForm'
import LoginPasswordForm from '@/components/login/LoginPasswordForm'
import NewDevice from '@/views/NewDevice'
import Devices from '@/views/Devices'
import Config from '@/views/Config'
import Signals from '@/views/Signals'
import UpdateSignal from '@/views/UpdateSignal'
import NewSignal from '@/views/NewSignal'
import NewCommand from '@/views/NewCommand'
import UpdateCommand from '@/views/UpdateCommand'
import UpdateDevice from '@/views/UpdateDevice'
import Commands from '@/views/Commands'
import Monitor from '@/views/Monitor'
// TEST
import DashBoardChart from '@/views/DashBoardChart'

Vue.use(Router)

var context = '/modbus'

const route = new Router({
  mode: 'history',
  routes: [
    {
      path: context + '/',
      name: 'dashBoardChart',
      component: DashBoardChart,
      meta: {
        private: true
      }
    },
    {
      path: context + '/login',
      name: 'login',
      component: Login,
      redirect: { name: 'login-form' },
      children: [
        {
          path: '',
          name: 'login-form',
          component: LoginForm
        },
        {
          path: 'password',
          name: 'password-form',
          component: LoginPasswordForm
        }
      ]
    },
    {
      path: context + '/newdevice',
      name: 'newDevice',
      component: NewDevice,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices',
      name: 'devices',
      component: Devices,
      meta: {
        private: true
      }
    },
    {
      path: context + '/config',
      name: 'config',
      component: Config,
      meta: {
        private: true
      }
    },
    {
      path: context + '/monitor',
      name: 'monitor',
      component: Monitor,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/updateDevice',
      name: 'updateDevice',
      component: UpdateDevice,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/signals',
      name: 'signals',
      component: Signals,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/signals/:signalId/updateSignal',
      name: 'updateSignal',
      component: UpdateSignal,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/signals/newSignal',
      name: 'newSignal',
      component: NewSignal,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/commands',
      name: 'commands',
      component: Commands,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/newCommand',
      name: 'newCommand',
      component: NewCommand,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:id/:signalId/:commandId/updateCommand',
      name: 'updateCommand',
      component: UpdateCommand,
      meta: {
        private: true
      }
    },
    {
      path: '/*',
      redirect: { name: 'dashBoardChart' },
      meta: {
        private: true
      }
    }
  ]
})

route.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.private)) {
    if (!sessionStorage.getItem('sessionToken')) {
      next({name: 'login'})
    } else {
      next()
    }
  } else {
    next()
  }
})

export default route



