<template>
  <div class="container">
    <ods-row :gutter="16">
      <ods-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <ods-module class="fixed-card">
          <ods-row slot="header" class="border">
            <h1 class="h1">{{$t('dashBoard.alerts')}}</h1>
          </ods-row>
          <ods-row>
            <ods-row>
              <ods-table :data="alerts" height="290" class="table-style" size="mini">
                <ods-table-column
                  :label="$t('alert.state')"
                  width="70">
                  <template slot-scope="scope">
                    <ods-tooltip effect="dark" :content="scope.row.state" placement="top">
                      <div :class="scope.row.state"></div>
                    </ods-tooltip>
                  </template>
                </ods-table-column>
                <ods-table-column
                  prop="date"
                  :label="$t('alert.date')"
                  width="100">
                </ods-table-column>
                <ods-table-column
                  prop="alertType"
                  :label="$t('alert.alertType')">
                </ods-table-column>
                <ods-table-column
                  prop="deviceId"
                  :label="$t('alert.deviceId')">
                </ods-table-column>
                 <ods-table-column
                  prop="signalId"
                  :label="$t('alert.signalId')"
                  width="100">
                </ods-table-column>
              </ods-table>
            </ods-row>
          </ods-row>
        </ods-module>
      </ods-col>
      <ods-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <ods-module class="fixed-card">
          <ods-row slot="header" class="border">
            <h1 class="h1">{{$t('dashBoard.volume')}}</h1>
          </ods-row>
          <ods-row :gutter="10" class="line-height">
            <ods-col :xs="13" :sm="13" :md="13" :lg="13" :xl="13">
              <ods-row style="margin-bottom: 20px;">
                <ods-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1" :offset="2">
                  <div class="punto_rojo"></div>
                </ods-col>
                 <ods-col :xs="9" :sm="9" :md="9" :lg="9" :xl="9">
                  <h1 class="legend">{{$t('dashBoard.signalError')}}</h1>
                </ods-col>
                <ods-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1" :offset="2">
                  <div class="punto_azul"></div>
                </ods-col>
                 <ods-col :xs="9" :sm="9" :md="9" :lg="9" :xl="9">
                  <h1 class="legend">{{$t('dashBoard.signalActive')}}</h1>
                </ods-col>
              </ods-row>
              <ods-row  class="gauge-row" type="flex" justify="space-between">
                <ods-col class="gauge-col">
                    <ods-chart v-if="gauge" type="gauge" :data="gauge" :size="230" :text="true" symbol="%"></ods-chart>
                </ods-col>
              </ods-row>
            </ods-col>
            <ods-col :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <ods-row :gutter="20" class="margin-top-50">
                  <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" :span="12">
                    <ods-row :gutter="10" class="border-bottom">
                      <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                        <ods-row>
                          <ods-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                            <ods-icon class="icon" name="broadcast" size="16" color="#6E767D"/>
                          </ods-col>
                          <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                            <h2 class="h2">{{$t('dashBoard.devices')}}</h2>
                          </ods-col>
                        </ods-row>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number">{{info.totalDevices}}</label>
                        </div>
                        <h2 class="legend">{{$t('dashBoard.total')}}</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number red">{{info.totalDeviceOnError}}</label>
                        </div>
                      </ods-col>
                    </ods-row>
                  </ods-col>
                  <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" :span="12">
                    <ods-row :gutter="10" class="padding-top-bottom">
                      <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                          <ods-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                            <i class="icon icon-icon_senales margin-img" style="font-size: 16px;"/>
                          </ods-col>
                          <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                            <h2 class="h2">{{$t('dashBoard.signals')}}</h2>
                          </ods-col>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number">{{info.totalSignals}}</label>
                        </div>
                        <h2 class="legend">{{$t('dashBoard.total')}}</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number red">{{info.totalSignalOnError}}</label>
                        </div>
                        <h2 class="legend red">{{$t('dashBoard.onError')}}</h2>
                      </ods-col>
                    </ods-row>
                  </ods-col>
                </ods-row>
            </ods-col>
          </ods-row>
        </ods-module>
      </ods-col>
    </ods-row>
    <ods-row>
      <ods-col>
        <ods-module class="graph-card" v-loading="loading">
          <ods-row slot="header" class="structure-header header-graph">
            <ods-row class="border">
              <ods-col :span="8" :offset="8">
                <div class="header-graph">
                  <h1 class="h1" style="margin-bottom: 10px;">{{$t('dashBoard.graph')}}</h1>
                </div >
              </ods-col>
              <ods-col>
                <ods-form :model="graphForm" ref="graphForm" :rules="graphFormRules">
                  <div class="header-graph" >
                    <ods-form-item prop="initDate" >
                      <ods-date-picker class="select-width-date"
                      v-model="graphForm.initDate"
                      type="date"
                      :placeholder="$t('dashBoard.initDate')"
                      format="yyyy/MM/dd"
                      value-format="timestamp">
                      </ods-date-picker>
                    </ods-form-item>
                    <ods-form-item>
                      <h4 class="h4" style="margin-right: 16px;">TO</h4>
                    </ods-form-item>
                    <ods-form-item prop="endDate" >
                      <ods-date-picker class="select-width-date"
                      v-model="graphForm.endDate"
                      type="date"
                      :placeholder="$t('dashBoard.endDate')"
                      format="yyyy/MM/dd"
                      value-format="timestamp">
                      </ods-date-picker>
                    </ods-form-item>
                    <ods-form-item prop="argument" >
                      <ods-select class="select-width" v-model="graphForm.argument" :placeholder="$t('dashBoard.argument')">
                        <ods-option
                          v-for="item in argument"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item prop="groupBy" >
                      <ods-select class="select-width" v-model="graphForm.groupBy" :placeholder="$t('dashBoard.groupBy')">
                        <ods-option
                          v-for="item in groupBy"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item prop="deviceId" >
                      <ods-select class="select-width" v-model="graphForm.deviceId" :placeholder="$t('dashBoard.device')" v-on:change="getSignals">
                        <ods-option
                          v-for="item in deviceIds"
                          :key="item"
                          :label="item"
                          :value="item">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item prop="signalId" >
                      <ods-select class="select-width" v-model="graphForm.signalId" :placeholder="$t('dashBoard.signal')">
                        <ods-option
                          v-for="item in signalIds"
                          :key="item"
                          :label="item"
                          :value="item">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item>
                      <ods-button size="small" @click="getResultGraph('graphForm')">{{$t('button.apply')}}</ods-button>
                    </ods-form-item>
                  </div>
                </ods-form>
              </ods-col>
            </ods-row>
          </ods-row>
          <ods-row v-if="graph === null" type="flex" justify="center" >
            <ods-row class="box">
              <h3 class="h4">{{$t('dashBoard.noData')}}</h3>
              <h4 class="h5">{{$t('dashBoard.dataGraph')}}</h4>
            </ods-row>
          </ods-row>
          <ods-row v-else-if="graph !== null  && graph.datasets.length === 0" type="flex" justify="center" >
            <ods-row class="box">
              <h3 class="h4">{{$t('dashBoard.noData')}}</h3>
              <h4 class="h5">{{$t('dashBoard.noDataGraph')}}</h4>
            </ods-row>
          </ods-row>
          <ods-row v-show="showGraph">
            <canvas id="chart"></canvas>
          </ods-row>
        </ods-module>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'
import Chart from 'chart.js'
import ChartData from '../chart-data.js'

export default {
  name: 'DashBoard',
  data () {
    return {
      ChartData: ChartData,
      graphForm: {
        initDate: '',
        endDate: '',
        argument: '',
        groupBy: '',
        deviceId: '',
        signalId: ''
      },
      showGraph: true,
      info: [],
      groupBy: [{
        value: '1h',
        label: 'Horario'
      }, {
        value: '1d',
        label: 'Diario'
      }, {
        value: '1w',
        label: 'Weekly'
      }],
      argument: [{
        value: 'mean',
        label: this.$t('argument.mean')
      }, {
        value: 'count',
        label: this.$t('argument.count')
      }],
      devicesList: [],
      deviceIds: [],
      signalIds: [],
      gauge: null,
      alerts: null,
      graph: null,
      loading: false,
      chart: null,
      graphFormRules: {
        initDate: [
          { required: true, message: 'Introduzca una fecha de inicio', trigger: 'blur' }
        ],
        endDate: [
          { required: true, message: 'Introduzca una fecha de fin', trigger: 'blur' }
        ],
        argument: [
          { required: true, message: 'Introduzca un argumento', trigger: 'blur' }
        ],
        groupBy: [
          { required: true, message: 'Introduzca una agrupación', trigger: 'blur' }
        ],
        deviceId: [
          { required: true, message: 'Introduzca un dipsositivo', trigger: 'blur' }
        ],
        signalId: [
          { required: true, message: 'Introduzca una señal', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getInfo()
    this.getGauge()
    this.getDevices()
    this.getAlerts()
    // this.interval = setInterval(function () {
    //   this.getAlerts()
    // }, 5000)
  },
  methods: {
    getInfo () {
      axios.get(this.$url + '/devices/info').then(response => {
        this.info = response.data
      })
    },
    getGauge () {
      this.gauge = null
      axios.get(this.$url + '/devices/gauge').then(response => {
        this.gauge = response.data
      })
    },
    getAlerts () {
      axios.get(this.$url + '/devices/alerts').then(response => {
        this.alerts = response.data
      })
    },
    getDevices () {
      axios.get(this.$url + '/devices').then(response => {
        this.devicesList = response.data
        var i
        for (i = 0; i < this.devicesList.length; i++) {
          this.deviceIds.push(this.devicesList[i].id)
        }
      })
    },
    getSignals () {
      this.graphForm.signalId = null
      var i
      for (i = 0; i < this.devicesList.length; i++) {
        if (this.devicesList[i].id === this.graphForm.deviceId) {
          this.signalIds.length = 0
          var j
          for (j = 0; j < this.devicesList[i].signals.length; j++) {
            this.signalIds.push(this.devicesList[i].signals[j].id)
          }
        }
      }
    },
    getResultGraph (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          axios.put(this.$url + '/devices/graph', this.graphForm)
            .then(response => {
              this.graph = null
              this.graph = response.data
              if (this.graph !== null && this.graph.datasets.length !== 0) {
                this.showGraph = true
                this.createChart('chart', this.ChartData)
              } else {
                this.showGraph = false
              }
              this.loading = false
              if (this.graph === null) {
                this.$notify({
                  title: 'Error',
                  message: this.$t('messagesBox.noDatabase'),
                  type: 'error'
                })
              } else if (this.graph.datasets.length === 0) {
                this.$notify({
                  title: 'Warning',
                  message: this.$t('messagesBox.noData'),
                  type: 'warning'
                })
              } else {
                this.$notify({
                  title: 'Success',
                  message: this.$t('messagesBox.graph'),
                  type: 'success'
                })
              }
            })
            .catch(error => {
              this.loading = false
              console.log(error)
              this.$notify({
                title: 'Error',
                message: this.$t('messagesBox.error'),
                type: 'error'
              })
            })
        } else {
          console.log('error submit!!')
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.incorrectForm'),
            type: 'warning'
          })
          return false
        }
      })
    },
    createChart (chartId, chartData) {
      if (this.showGraph) {
        const ctx = document.getElementById(chartId)
        if (this.chart !== null) {
          this.chart.destroy()
        }
        this.chart = new Chart(ctx, {
          type: chartData.type,
          data: this.graph,
          options: chartData.options
        })
      }
    }
  }
  //   beforeDestroy: function () {
  //   clearInterval(this.interval)
  // }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.container {
  margin-top: 48px;
  margin-left: 18px;
  margin-right: 18px;
}

.number {
  font-size: 35px;
  font-weight: 500;
  margin-bottom: 10px;
}

.fixed-card {
  overflow: hidden;
  position: relative;
  height: 400px;
  margin-bottom: 40px;
}
.graph-card{
  /* height: 490px; */
  margin-bottom: 40px;
  padding-top: 0px;
}
.border {
  padding-bottom: 20px;
  border-bottom: solid;
  border-width: 1px;
  border-color:#D8D8D8;
}
.line-height {
  line-height: 15px;
}
.h1 {
  color: #060e14;
  font-size: 16px;
  font-weight: 500;
}
.h2 {
  font-size: 11px;
  text-transform: uppercase;
  color: #060E14;
}
.h3 {
  color: grey;
  font-size: 13px;
}
.h4 {
  color: grey;
  font-size: 9px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.h5 {
  color: #060E14;
  font-size: 14px;
}
.icon {
  color: #6E767D;
}
.box {
  background: #F7F9FA;
  padding: 20px 30px 30px 30px;
  width: 250px;
  line-height: 20px;
}
.legend {
  color: grey;
  font-size: 9px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.select-margin {
   margin-right: 16px;
}

.structure-header {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.header-graph {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
}
.select-width {
  margin-right: 16px;
  max-width: 120px;
}
.select-width-date {
  margin-right: 16px;
  max-width: 140px;
}
.border-bottom{
  border-bottom: solid;
  border-color: rgb(170, 170, 170);
  border-width: 1px;
}
.padding-top-bottom{
  padding-top: 21px;
  padding-bottom: 10px;
}
.margin-top-50 {
  margin-top: 50px;
}
.punto_rojo {
     width: 8px;
     height: 8px;
     -moz-border-radius: 50%;
     -webkit-border-radius: 50%;
     border-radius: 50%;
     background: #DE2F2F;
     margin-top: 3px;
}
.punto_azul {
     width: 8px;
     height: 8px;
     -moz-border-radius: 50%;
     -webkit-border-radius: 50%;
     border-radius: 50%;
     background: #79C5B4;
     margin-top: 3px;
}
.red {
  color: #DE2F2F
}
.margin-left-9{
  margin-left: 9px;
}
.margin-img {
  width: 16px;
  margin-top: -2px;
}
.gauge-row {
  display: flex;
  align-items: center;
  margin-left: auto;
  width: 250px;
  margin-right: auto;
}
.gauge-col {
  display: flex;
  align-items: end;
  width: 250px;
}
.table-style {
  font-size: 12px;
  line-height: 1px;
}
.INFO {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #79C5B4;
  cursor: pointer;
}
.WARNING {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #1D5075;
  cursor: pointer;
}
.ERROR {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #DE2F2F;
  cursor: pointer;
}
</style>



