<template>
  <div>
    <ods-row type="flex" justify="space-between" class="top-buttons">
      <ods-col class="button-right" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
      <ods-col class="button-right" :span="24">
        <h3 class="h3">{{$t('button.newSignal')}}</h3>
        <ods-button icon="ods-icon-plus" circle size="small" v-on:click="toNewSignal"></ods-button>
      </ods-col>
    </ods-row>
    <ods-row type="flex" class="top-header">
      <ods-col :span="24">
        <h1 class="title">{{$t('title.signals', [$route.params.id])}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-table stripe v-loading="loading" :data="signalList" :default-sort = "{prop: 'id', order: 'ascending'}"  size=small>
        <ods-table-column type="expand">
          <template slot-scope="props">
            <ods-row>
              <ods-col class="props" :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                <h2 class="titleprops">{{$t('signalForm.description')}}:</h2>
              </ods-col>
              <ods-col class="props" :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <h2 class="value">{{ props.row.description }}</h2>
              </ods-col>
            </ods-row>
            <ods-row>
              <ods-col class="props" :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                <h2 class="titleprops">{{$t('signalForm.dataType')}}:</h2>
              </ods-col>
              <ods-col class="props" :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <h2 class="value">{{ props.row.dataType }}</h2>
              </ods-col>
            </ods-row>
            <ods-row>
              <ods-col class="props" :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                <h2 class="titleprops">{{$t('signalForm.convFactor')}}:</h2>
              </ods-col>
              <ods-col class="props" :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <h2 class="value">{{ props.row.convFactor }}</h2>
              </ods-col>
            </ods-row>
            <ods-row>
              <ods-col class="props" :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
                <h2 class="titleprops">{{$t('signalForm.bigEndian')}}:</h2>
              </ods-col>
            <ods-col class="props" :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <h2 class="value">{{ props.row.bigEndian }}</h2>
              </ods-col>
            </ods-row>
          </template>
        </ods-table-column>
        <ods-table-column
          prop="id"
          sortable
          :label="$t('signalForm.id')">
        </ods-table-column>
        <ods-table-column
          prop="businessId"
          :label="$t('signalForm.businessId')">
        </ods-table-column>
        <ods-table-column
          prop="registerType"
          :label="$t('signalForm.registerType')"
          width="100">
        </ods-table-column>
        <ods-table-column
          prop="signalType"
          :label="$t('signalForm.signalType')">
        </ods-table-column>
        <ods-table-column
          prop="register"
          :label="$t('signalForm.register')"
          width="100">
        </ods-table-column>
        <ods-table-column
          prop="unit"
          :label="$t('signalForm.unit')"
          width="80">
        </ods-table-column>
        <ods-table-column
          fixed="right"
          :label="$t('signalForm.operation')"
          width="160">
          <template slot-scope="scope">
            <ods-row style="margin-left: -20px;">
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-tooltip effect="dark" :content="$t('button.update')" placement="top">
                  <ods-button circlestyle="width=20px;" v-on:click="toUpdateSignal(scope.row)" type="text" size="small" icon="ods-icon-arrow-right"></ods-button>
                </ods-tooltip>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-tooltip effect="dark" :content="$t('button.delete')" placement="top">
                  <ods-button circlestyle="width=20px;" @click="setDialogDelete(scope.row)" type="text" size="small" icon="ods-icon-delete"></ods-button>
                </ods-tooltip>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-tooltip effect="dark" :content="$t('button.execute')" placement="top">
                  <ods-button v-if="scope.row.isCommandable" style="width=20px;" @click="setDialogValue(scope.row)" type="text" size="small" icon="ods-icon-hand"></ods-button>
                </ods-tooltip>
              </ods-col>
            </ods-row>
          </template>
        </ods-table-column>
      </ods-table>
    </ods-row>
    <!-- DIALOG DELETE SIGNAL -->
    <ods-dialog :title="$t('deleteDialogSignal.title')" :visible.sync="dialogVisibleDelete" width="30%">
      <span>{{$t('deleteDialogSignal.message')}}</span>
      <span slot="footer" class="dialog-footer">
        <ods-button type="text" @click="dialogVisibleDelete = false">{{$t('button.cancel')}}</ods-button>
        <ods-button type="primary" @click="deleteSignal(idToDelete)">{{$t('button.confirm')}}</ods-button>
      </span>
    </ods-dialog>
    <!-- DIALOG EXECUTE COMMAND -->
    <ods-dialog :title="$t('sendValueSignal.title')" :visible.sync="dialogVisibleValue" width="40%">
      <ods-row class="infoSignal">
        <ods-row>
          <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
            <ods-row>
              <ods-col class="h2" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h2>{{$t('signalForm.id')}}:</h2>
              </ods-col>
              <ods-col class="h4" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h4>{{signalCommand.id}}</h4>
              </ods-col>
            </ods-row>
            <ods-row>
              <ods-col class="h2" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h2>{{$t('signalForm.businessId')}}:</h2>
              </ods-col>
              <ods-col class="h4" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h4>{{signalCommand.businessId}}</h4>
              </ods-col>
            </ods-row>
          </ods-col>
          <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
            <ods-row>
              <ods-col class="h2" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h2>{{$t('signalForm.registerType')}}:</h2>
              </ods-col>
              <ods-col class="h4" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h4>{{signalCommand.registerType}}</h4>
              </ods-col>
            </ods-row>
            <ods-row>
              <ods-col class="h2" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h2>{{$t('signalForm.register')}}:</h2>
              </ods-col>
              <ods-col class="h4" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <h4>{{signalCommand.register}}</h4>
              </ods-col>
            </ods-row>
          </ods-col>
        </ods-row>
        <ods-row>
          <ods-col class="h2" :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
            <h2>{{$t('signalForm.description')}}:</h2>
          </ods-col>
          <ods-col class="h4" :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
            <h4>{{signalCommand.description}}</h4>
          </ods-col>
        </ods-row>
      </ods-row>
      <ods-row class="infoSignal">
        <ods-col class="h2" :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
          <h2 style="margin-right: 10px; margin-top: 3px;">{{$t('sendValueSignal.value')}}</h2>
        </ods-col>
        <ods-col class="h2" :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
          <ods-input type="number" size="deci" v-model="valueToSend" ></ods-input>
        </ods-col>
      </ods-row>
      <ods-row type="flex" justify="space-between" style="margin-top: 40px;">
        <ods-button type="text" size="medium" @click="dialogVisibleValue = false">{{$t('button.cancel')}}</ods-button>
        <ods-button type="primary" size="medium" @click="sendCommand(signalCommand.id)" v-loading.fullscreen.lock="fullscreenLoading">{{$t('button.execute')}}</ods-button>
      </ods-row>
    </ods-dialog>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'Signals',
  data () {
    return {
      idToDelete: '',
      signalList: [],
      dialogVisibleDelete: false,
      dialogVisibleValue: false,
      valueToSend: '',
      signalCommand: '',
      loading: true,
      fullscreenLoading: false
    }
  },
  created () {
    this.getSignals()
  },
  methods: {
    getSignals () {
      this.loading = true
      axios.get(this.$url + '/devices/' + this.$route.params.id + '/signals').then(response => {
        this.signalList = response.data
        this.loading = false
      })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
          this.loading = false
        })
    },
    toUpdateSignal (row) {
      this.$router.push({ name: 'updateSignal', params: { id: this.$route.params.id, signalId: row.id } })
    },
    toNewSignal () {
      this.$router.push({ name: 'newSignal', params: { id: this.$route.params.id } })
    },
    deleteSignal (id) {
      axios.delete(this.$url + '/devices/' + this.$route.params.id + '/signals/' + id)
        .then(response => {
          this.getSignals()
          this.dialogVisibleDelete = false
          this.$notify({
            title: 'Success',
            message: this.$t('messagesBox.signalDeleted', [id]),
            type: 'success'
          })
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
          this.$notify({
            title: 'Error',
            message: error.response.data.cause,
            type: 'error'
          })
        })
    },
    sendCommand (id) {
      this.fullscreenLoading = true
      axios.put(this.$url + '/devices/' + this.$route.params.id + '/signals/' + id + '/' + this.valueToSend)
        .then(response => {
          this.dialogVisibleValue = false
          this.fullscreenLoading = false
          this.$notify({
            title: 'Success',
            message: this.$t('messagesBox.execute'),
            type: 'success'
          })
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
          this.dialogVisibleValue = false
          this.fullscreenLoading = false
          this.$notify({
            title: 'Error',
            message: error.response.data.cause,
            type: 'error'
          })
        })
    },
    setDialogDelete (row) {
      this.dialogVisibleDelete = true
      this.idToDelete = row.id
    },
    setDialogValue (row) {
      this.valueToSend = ''
      this.dialogVisibleValue = true
      this.signalCommand = row
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 80%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.top-header {
  display: flex;
  align-items: center;
  width: 80%;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h2 {
  margin-top: 10px;
  font-size: 12px;
  font-weight: bold;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-left: 40px;
}
.h4 {
  margin-top: 8px;
  font-size: 13px;
  color: #2E6C99;
}
.table {
  background-color: #FFFFFF;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
  margin-top: 20px;
}
.title {
  font-size: 22px;
  color: #060E14;
  margin: 0 0 35px 0;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
.button-header {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
  margin-right: 24px;
}
.button-right {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
  margin-right: 24px;
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.props {
  font-size: 12px;
  margin-bottom: 10px;
}
.titleprops {
    margin-right: 5px;
}
.value {
  color: #2E6C99;
}
.infoSignal {
  border-radius: 5px;
  border: solid;
  border-width: 1px;
  border-color: #E4EEF2;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  padding: 5px 5px 15px 10px;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}

/deep/ .ods-table__expand-icon {
  height: 25px;
  margin-bottom: 12px;
}
</style>



