<template>
  <div>
    <ods-row type="flex" class="top-header">
      <ods-col class="button-left" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
      <ods-col :span="24">
        <h1 class="h1">{{$t('commandForm.titleNewCommand')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-form label-position="top" :model="commandForm" :rules="commandFormRules" ref="commandForm">
        <!-- CONFIGURACION -->
        <ods-row>
          <ods-row>
            <ods-row class="padding-top-bottom">
              <h2 class="h2">{{$t('deviceform.configuration')}}</h2>
            </ods-row>
            <ods-row :gutter="30">
              <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <ods-form-item :label="$t('commandForm.deviceId')" prop="deviceId">
                  <ods-input disabled v-model="commandForm.deviceId"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <ods-form-item :label="$t('commandForm.commandId')" prop="commandId">
                  <ods-input v-model="commandForm.commandId"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <ods-form-item :label="$t('commandForm.signalId')" prop="signalId">
                  <ods-select v-model="commandForm.signalId" :placeholder="$t('commandForm.selectSignal')">
                    <ods-option
                      v-for="item in signalsIds"
                      :key="item"
                      :label="item"
                      :value="item">
                    </ods-option>
                  </ods-select>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                <ods-form-item :label="$t('commandForm.value')" prop="value">
                  <ods-input type="number" v-model="commandForm.value"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                <ods-form-item :label="$t('commandForm.description')" prop="description">
                  <ods-input v-model="commandForm.description" type="textarea" :rows="2" placeholder=""></ods-input>
                </ods-form-item>
              </ods-col>
            </ods-row>
          </ods-row>
        </ods-row>
      </ods-form>
    </ods-row>
    <ods-row class="top-header" type="flex" justify="end">
      <ods-col class="button-right">
        <ods-button @click="createCommand('commandForm')" size="medium" v-loading.fullscreen.lock="fullscreenLoading">{{$t('button.create')}}</ods-button>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'NewCommand',
  data () {
    return {
      commandForm: {
        deviceId: this.$route.params.id,
        signalId: '',
        commandId: '',
        description: '',
        value: ''
      },
      commandData: {
        id: '',
        description: '',
        value: ''
      },
      signalList: [],
      signalsIds: [],
      commandFormRules: {
        signalId: [
          { required: true, message: 'Por favor, introduzca una señal', trigger: 'blur' }
        ],
        commandId: [
          { required: true, message: 'Por favor, introduzca un command id', trigger: 'blur' }
        ],
        description: [
          { required: true, message: 'Por favor, introduzca una descripción', trigger: 'blur' }
        ],
        value: [
          { required: true, message: 'Por favor, introduzca un valor', trigger: 'blur' }
        ]
      },
      fullscreenLoading: false
    }
  },
  created () {
    this.getSignals()
  },
  methods: {
    getSignals () {
      axios.get(this.$url + '/devices/' + this.$route.params.id + '/signals')
        .then(response => {
          this.signalList = response.data
          var i
          for (i = 0; i < this.signalList.length; i++) {
            if (this.signalList[i].registerType === 'HR' || this.signalList[i].registerType === 'CS') {
              this.signalsIds.push(this.signalList[i].id)
            }
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
        })
    },
    createCommand (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.fullscreenLoading = true
          this.commandData.id = this.commandForm.commandId
          this.commandData.description = this.commandForm.description
          this.commandData.value = this.commandForm.value
          axios.post(this.$url + '/devices/' + this.$route.params.id + '/signals/' + this.commandForm.signalId + '/command', JSON.stringify(this.commandData))
            .then(response => {
              this.fullscreenLoading = false
              this.$router.push({name: 'commands'})
              this.$notify({
                title: 'Success',
                message: this.$t('messagesBox.commandCreated', [this.commandForm.commandId]),
                type: 'success'
              })
            })
            .catch(error => {
              this.fullscreenLoading = false
              console.log(JSON.stringify(error.response.data))
              this.$notify({
                title: 'Error',
                message: this.$t('messagesBox.commandExist', [this.commandForm.commandId]),
                type: 'error'
              })
            })
        } else {
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.incorrectForm'),
            type: 'warning'
          })
          return false
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 55%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.top-header {
  display: flex;
  align-items: center;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h2 {
  font-size: 16px;
  text-transform: capitalize;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-top: -5px;
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.button-right {
  margin-bottom: 20px;
  margin-top: 20px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: right;
  position: relative;
  width: auto;
  height: 100%;
}
.padding-top-bottom {
  padding-bottom: 40px;
  padding-top: 30px;
}
</style>



