<template>
<div>
    <ods-row type="flex" class="top-buttons">
      <ods-col :xs="16" :sm="9" :md="8" :lg="6" :xl="4" class="margin-left">
        <ods-row type="flex">
          <ods-col class="select border-right">
            <h3 class="select-text">{{$t('monitor.selectDevice')}}</h3>
            <ods-select v-model="deviceId" v-on:change="getSignals">
              <ods-option
                v-for="item in devicesList"
                :key="item.id"
                :label="item.id"
                :value="item.id">
              </ods-option>
            </ods-select>
          </ods-col>
        </ods-row>
      </ods-col>
      <ods-col :xs="7" :sm="14" :md="15" :lg="17" :xl="19" style="margin-left:10px;">
        <h3 class="update-text">{{$t('monitor.lastUpdate')}}</h3>
        <h3 class="update-value">{{datetime}}hs</h3>
      </ods-col>
    </ods-row>
    <ods-row type="flex" justify="space-between" class="top-header">
      <ods-col :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
        <h1 class="title">{{deviceId}}</h1>
      </ods-col>
      <ods-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
          <div v-if="deviceOnError && deviceId != ''" class="divError">
            <h2 class="onError">{{onError}}</h2>
          </div>
          <div v-if="!deviceOnError && deviceId != ''" class="divActive">
            <h2 class="onError">{{onError}}</h2>
          </div>
        </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-row :gutter="20">
        <ods-col>
          <ods-row>
            <ods-table stripe :data="signals" :default-sort = "{prop: 'id', order: 'ascending'}" style="width: 100%" size=mini>
              <ods-table-column prop="id" sortable :label="$t('signalForm.id')" width="100"></ods-table-column>
              <ods-table-column prop="businessId" :label="$t('signalForm.businessId')"></ods-table-column>
              <ods-table-column prop="description" :label="$t('signalForm.description')"></ods-table-column>
              <ods-table-column prop="signalType" :label="$t('signalForm.signalType')"></ods-table-column>
              <ods-table-column prop="unit" :label="$t('signalForm.unit')" width="80"></ods-table-column>
              <ods-table-column prop="convertedValue" :label="$t('signalForm.value')" width="130"></ods-table-column>
            </ods-table>
          </ods-row>
        </ods-col>
      </ods-row>
    </ods-row>
</div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'Monitor',
  data () {
    return {
      devicesList: [],
      currentdate: '',
      datetime: '',
      deviceId: '',
      signals: '',
      deviceOnError: '',
      onError: 'ACTIVE',
      interval: null
    }
  },
  created () {
    this.getDevices()
    this.interval = setInterval(function () {
      this.getDevices()
    }.bind(this), 2000)
  },
  methods: {
    getDevices () {
      axios.get(this.$url + '/devices').then(response => {
        this.devicesList = response.data
        this.currentdate = new Date()
      })
      this.getSignals()
      this.getDate()
    },
    getSignals () {
      this.signals = []
      var i
      for (i = 0; i < this.devicesList.length; i++) {
        if (this.devicesList[i].id === this.deviceId) {
          if (this.devicesList[i].onError) {
            this.onError = 'ERROR'
          } else {
            this.onError = 'ACTIVE'
          }
          this.deviceOnError = this.devicesList[i].onError
          this.signals = this.devicesList[i].signals
          break
        }
      }
    },
    getDate () {
      var today = new Date()
      var dd = today.getDate()
      var MM = today.getMonth() + 1 // January is 0!
      var yyyy = today.getFullYear()
      var hh = today.getHours()
      var mm = today.getMinutes()
      var ss = today.getSeconds()

      if (dd < 10) {
        dd = '0' + dd
      }
      if (MM < 10) {
        MM = '0' + MM
      }
      if (hh < 10) {
        hh = '0' + hh
      }
      if (mm < 10) {
        mm = '0' + mm
      }
      if (ss < 10) {
        ss = '0' + ss
      }
      this.datetime = dd + '/' + MM + '/' + yyyy + ' - ' + hh + ':' + mm + ':' + ss
    }
  },
  beforeDestroy: function () {
    clearInterval(this.interval)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 70%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.title {
  font-size: 22px;
  color: #060E14;
  margin: 0 0 35px 0;
  height: 25px;
}
.top-header {
  display: flex;
  align-items: center;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  margin-top: 50px;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
.select {
  display: flex;
  color: #888A91;
  font-size: 12px;
}
.select-text {
  margin-top: 10px;
  width: 100%;
}
.margin-left {
  margin-left: 24px;
}
.border-right {
  border-right: solid;
  border-width: 2px;
  border-color: #D8D8D8;
  padding-right: 10px;
}
.update-text {
 color: #868D93;
 margin-bottom: 8px;
 font-size: 10px;
 text-transform: uppercase;
}
.update-value {
 font-size: 14px;
}

.divActive {
  border-radius: 6px;
  background-color: #79C5B4;
  text-align: center;
}
.divError {
  border-radius: 6px;
  background-color: #DE2F2F;
  text-align: center;
}
.onError {
  color: white;
  font-size: 15px;
  padding: 5px 10px;
  margin: 0 0 35px 0;
  // margin-top: -25px;
}
</style>



