<template>
  <div>
    <ods-row type="flex" class="top-buttons">
      <ods-col class="button-right" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
    </ods-row>
    <ods-row type="flex" class="top-header">
      <ods-col :span="24">
        <h1 class="h1">{{$t('signalForm.titleNew')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-form label-position="top" :model="signalForm" :rules="signalFormRules" ref="signalForm">
        <!-- DATOS BÄSICOS -->
        <ods-row class="border-bottom">
          <ods-row class="padding-top-bottom">
            <h2 class="h2">{{$t('deviceform.basicData')}}</h2>
          </ods-row>
          <ods-row :gutter="30">
            <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
              <ods-form-item :label="$t('signalForm.id')" prop="id" >
                <ods-input v-model="signalForm.id" auto-complete="id"></ods-input>
              </ods-form-item>
            </ods-col>
            <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
              <ods-form-item :label="$t('signalForm.businessId')" prop="businessId" >
                <ods-input v-model="signalForm.businessId" auto-complete="businessId"></ods-input>
              </ods-form-item>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-form-item :label="$t('signalForm.description')" prop="description">
              <ods-input v-model="signalForm.description" auto-complete="description" type="textarea" :rows="2" placeholder=""></ods-input>
            </ods-form-item>
          </ods-row>
        </ods-row>
        <!-- CONFIGURACION -->
        <ods-row>
          <ods-row>
            <ods-row class="padding-top-bottom">
              <h2 class="h2">{{$t('deviceform.configuration')}}</h2>
            </ods-row>
            <ods-row :gutter="30">
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.register')" prop="register" >
                  <ods-input type="number" v-model="signalForm.register" auto-complete="register"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.registerType')" prop="registerType">
                  <ods-select v-model="signalForm.registerType">
                    <ods-option
                      v-for="item in registerType"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </ods-option>
                  </ods-select>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.signalType')" prop="signalType" >
                  <ods-input v-model="signalForm.signalType" auto-complete="signalType"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.dataType')" prop="dataType">
                  <ods-select v-model="signalForm.dataType">
                    <ods-option
                      v-for="item in dataType"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </ods-option>
                  </ods-select>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.unit')" prop="unit" >
                  <ods-input v-model="signalForm.unit" auto-complete="unit"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.convFactor')" prop="convFactor" >
                  <ods-input type="number" v-model="signalForm.convFactor" auto-complete="convFactor"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.bigEndian')" prop="bigEndian">
                  <ods-checkbox v-model="signalForm.bigEndian"></ods-checkbox>
                </ods-form-item>
              </ods-col>
              <ods-col v-if="signalForm.registerType === 'HR' || signalForm.registerType === 'CS'" :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
                <ods-form-item :label="$t('signalForm.commandable')" prop="bigEndian">
                  <ods-checkbox v-model="signalForm.isCommandable"></ods-checkbox>
                </ods-form-item>
              </ods-col>
            </ods-row>
          </ods-row>
        </ods-row>
      </ods-form>
    </ods-row>
    <ods-row class="top-header" type="flex" justify="end">
      <ods-col class="button-right">
        <ods-button @click="createSignal('signalForm')" size="medium" v-loading.fullscreen.lock="fullscreenLoading">{{$t('button.create')}}</ods-button>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'newSignal',
  data () {
    return {
      signalForm: {
        bigEndian: false
      },
      signalFormRules: {
        id: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        businessId: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        signalType: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        unit: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        convFactor: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        description: [
          { required: false, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        registerType: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        register: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        dataType: [
          { required: true, message: this.$t('validation.required'), trigger: 'blur' }
        ],
        bigEndian: [
          { required: false, message: this.$t('validation.required'), trigger: 'blur' }
        ]
      },
      registerType: [{
        value: 'HR',
        label: 'Holding Register'
      }, {
        value: 'IR',
        label: 'Input Register'
      }, {
        value: 'IS',
        label: 'Input Status'
      }, {
        value: 'CS',
        label: 'Coil Status'
      }],
      dataType: [{
        value: 'SINT16',
        label: 'SINT16'
      }, {
        value: 'SINT32',
        label: 'SINT32'
      }, {
        value: 'SINT64',
        label: 'SINT64'
      }, {
        value: 'UINT16',
        label: 'UINT16'
      }, {
        value: 'UINT32',
        label: 'UINT32'
      }, {
        value: 'UINT64',
        label: 'UINT64'
      }, {
        value: 'CHART16',
        label: 'CHART16'
      }, {
        value: 'CHART32',
        label: 'CHART32'
      }, {
        value: 'BOOLEAN',
        label: 'BOOLEAN'
      }, {
        value: 'FLOAT32',
        label: 'FLOAT32'
      }],
      fullscreenLoading: false
    }
  },
  methods: {
    createSignal (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.fullscreenLoading = true

          if (this.signalForm.registerType === 'IR' || this.signalForm.registerType === 'IS') {
            this.signalForm.isCommandable = false
          }
          axios.post(this.$url + '/devices/' + this.$route.params.id + '/signals', JSON.stringify(this.signalForm))
            .then(response => {
              this.fullscreenLoading = false
              this.$router.push({ name: 'signals', params: { id: this.$route.params.id } })
              this.$notify({
                title: 'Success',
                message: this.$t('messagesBox.signalCreated', [this.signalForm.id]),
                type: 'success'
              })
            })
            .catch(error => {
              this.fullscreenLoading = false
              console.log(JSON.stringify(error.response))
              this.$notify({
                title: 'Error',
                message: error.response.data.cause,
                type: 'error'
              })
            })
        } else {
          console.log('error submit!!')
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.incorrectForm'),
            type: 'warning'
          })
          return false
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 55%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.container-button {

  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;

}
.top-header {
  display: flex;
  align-items: center;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h2 {
  font-size: 16px;
  text-transform: capitalize;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-top: -40px;
  margin-left: 40px;
}
.select {
  margin-top: 22px;
  margin-bottom: 39px;
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.button-right {
  margin-bottom: 20px;
  margin-top: 20px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: right;
  position: relative;
  width: auto;
  height: 100%;
}

.border-bottom {
  border-bottom: solid;
  border-width: 1px;
  border-color:#D8D8D8;
}
.padding-top-bottom {
  padding-bottom: 40px;
  padding-top: 30px;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
</style>



