<template>
  <div>
    <ods-row type="flex" class="top-header">
      <ods-col :span="24">
        <h1 class="title">{{$t('config.title')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-row class="margin-bottom">
          <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
            <h1 class="h1">{{$t('config.lang')}}</h1>
          </ods-col>
          <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
            <ods-select v-model="$i18n.locale">
              <ods-option
                v-for="item in langs"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </ods-option>
            </ods-select>
          </ods-col>
      </ods-row>
      <ods-row>
          <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
            <h1 class="h1">{{$t('config.format')}}</h1>
          </ods-col>
          <ods-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
            <ods-select v-model="bulkFormat" v-on:change="setBulkFormat">
              <ods-option
                v-for="data in format"
                :key="data.value"
                :label="data.label"
                :value="data.value">
              </ods-option>
            </ods-select>
          </ods-col>
      </ods-row>
    </ods-row>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Config',
  data () {
    return {
      langs: [
        {
          label: 'Español',
          value: 'es'
        },
        {
          label: 'English',
          value: 'en'
        }],
      bulkFormat: '',
      format: [
        {
          label: this.$t('config.individual'),
          value: false
        },
        {
          label: this.$t('config.group'),
          value: true
        }]
    }
  },
  created () {
    this.getBulkFormat()
  },
  methods: {
    getBulkFormat () {
      axios.get(this.$url + '/devices/bulkFormat')
        .then(response => {
          if (response.data) {
            this.bulkFormat = this.$t('config.group')
          } else {
            this.bulkFormat = this.$t('config.individual')
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
        })
    },
    setBulkFormat () {
      axios.put(this.$url + '/devices/bulkFormat/' + this.bulkFormat)
        .then(response => {
          this.$notify({
            title: 'Success',
            message: this.$t('messagesBox.bulkFormatChange'),
            type: 'success'
          })
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
          this.$notify({
            title: 'Error',
            message: this.$t('messagesBox.error', [this.deviceForm.id]),
            type: 'error'
          })
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 55%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.top-header {
  display: flex;
  align-items: center;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
}
.title {
  font-size: 22px;
  color: #060E14;
  margin: 40px 0 0 0;
}
.h1 {
  margin-top: 7px;
}
.margin-bottom {
  margin-bottom: 10px;
}
</style>



