<template>
  <div>
    <ods-row type="flex" class="top-buttons">
      <ods-col class="button-right" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
    </ods-row>
    <ods-row type="flex" class="top-header">
      <ods-col :span="24">
        <h1 class="h1">{{$t('deviceform.updateDevice')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-form label-position="top" :model="deviceForm" :rules="deviceFormRules" ref="deviceForm">
        <!-- DATOS BÄSICOS -->
        <ods-row class="border-bottom">
          <ods-row class="padding-top-bottom">
            <h2 class="h2">{{$t('deviceform.basicData')}}</h2>
          </ods-row>
          <ods-row :gutter="30">
            <ods-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
              <ods-form-item :label="$t('deviceform.id')" prop="id">
                <ods-input v-model="deviceForm.id" auto-complete="id" :disabled="true"></ods-input>
              </ods-form-item>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              <ods-form-item :label="$t('deviceform.protocolType')" prop="protocolType">
                <ods-select v-model="deviceForm.protocolType" :disabled="true">
                  <ods-option
                    v-for="item in protocolType"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </ods-option>
                </ods-select>
              </ods-form-item>
            </ods-col>
          </ods-row>
          <ods-row :gutter="30">
            <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              <ods-form-item :label="$t('deviceform.manufacturer')" prop="manufacturer">
                <ods-input v-model="deviceForm.manufacturer" auto-complete="manufacturer"></ods-input>
              </ods-form-item>
            </ods-col>
            <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              <ods-form-item :label="$t('deviceform.model')" prop="model">
                <ods-input v-model="deviceForm.model" auto-complete="model"></ods-input>
              </ods-form-item>
            </ods-col>
            <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              <ods-form-item :label="$t('deviceform.deviceType')" prop="deviceType">
                <ods-input v-model="deviceForm.deviceType" auto-complete="deviceType"></ods-input>
              </ods-form-item>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-form-item :label="$t('deviceform.info')" prop="info">
              <ods-input v-model="deviceForm.info" auto-complete="info" type="textarea" :rows="2" placeholder=""></ods-input>
            </ods-form-item>
          </ods-row>
        </ods-row>
        <!-- CONFIGURACION -->
        <ods-row>
          <ods-row>
            <ods-row class="padding-top-bottom">
              <h2 class="h2">{{$t('deviceform.configuration')}}</h2>
            </ods-row>
            <ods-row :gutter="30">
              <div v-show="deviceForm.protocolType === 'TCP'">
                <ods-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
                  <ods-form-item :label="$t('deviceform.ip')" prop="ip">
                    <ods-input v-model="deviceForm.ip" auto-complete="ip"></ods-input>
                  </ods-form-item>
                </ods-col>
                <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                  <ods-form-item :label="$t('deviceform.port')" prop="port">
                    <ods-input type="number" v-model="deviceForm.port" auto-complete="port"></ods-input>
                  </ods-form-item>
                </ods-col>
              </div>
              <div v-show="deviceForm.protocolType === 'RTU'">
                <ods-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
                  <ods-form-item :label="$t('deviceform.serial')" prop="serial">
                    <ods-input v-model="deviceForm.serial" auto-complete="serial"></ods-input>
                  </ods-form-item>
                </ods-col>
                <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                  <ods-form-item :label="$t('deviceform.bauds')" prop="bauds">
                    <ods-input type="number" v-model="deviceForm.bauds" auto-complete="bauds"></ods-input>
                  </ods-form-item>
                </ods-col>
              </div>
            </ods-row>
            <ods-row :gutter="30">
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-form-item :label="$t('deviceform.address')" prop="address">
                  <ods-input type="number" v-model="deviceForm.address" auto-complete="address"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-form-item :label="$t('deviceform.monitoringBlocks')" prop="monitoringBlocks">
                  <ods-input type="number" v-model="deviceForm.monitoringBlocks" auto-complete="monitoringBlocks"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-form-item :label="$t('deviceform.monitoringTimeMs')" prop="monitoringTimeMs">
                  <ods-input type="number" v-model="deviceForm.monitoringTimeMs" auto-complete="monitoringTimeMs"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-form-item :label="$t('deviceform.monitoringDelayMs')" prop="monitoringDelayMs">
                  <ods-input type="number"  v-model="deviceForm.monitoringDelayMs" auto-complete="monitoringDelayMs"></ods-input>
                </ods-form-item>
              </ods-col>
              <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                <ods-form-item :label="$t('deviceform.monitoringTimeoutSec')" prop="monitoringTimeoutSec">
                  <ods-input type="number" v-model="deviceForm.monitoringTimeoutSec" auto-complete="monitoringTimeoutSec"></ods-input>
                </ods-form-item>
              </ods-col>
            </ods-row>
          </ods-row>
        </ods-row>
      </ods-form>
    </ods-row>
    <ods-row class="top-header" type="flex" justify="end">
      <ods-col class="button-right">
        <ods-button @click="updateDevice('deviceForm')" size="medium" v-loading.fullscreen.lock="fullscreenLoading">{{$t('button.update')}}</ods-button>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'updateDevice',
  data () {
    return {
      deviceForm: {},
      deviceFormRules: {
        id: [
          { required: true, message: 'Por favor, introduzca un id', trigger: 'blur' }
        ],
        ip: [
          { required: false, message: 'Por favor, introduzca un ip', trigger: 'blur' }
        ],
        port: [
          { required: false, message: 'Por favor, introduzca un port', trigger: 'blur' }
        ],
        serial: [
          { required: false, message: 'Por favor, introduzca un serial', trigger: 'blur' }
        ],
        bauds: [
          { required: false, message: 'Por favor, introduzca unos baudios', trigger: 'blur' }
        ],
        address: [
          { required: true, message: 'Por favor, introduzca un address', trigger: 'blur' }
        ],
        manufacturer: [
          { required: true, message: 'Por favor, introduzca un manufacturer', trigger: 'blur' }
        ],
        model: [
          { required: true, message: 'Por favor, introduzca un model', trigger: 'blur' }
        ],
        deviceType: [
          { required: true, message: 'Por favor, introduzca un deviceType', trigger: 'blur' }
        ],
        monitoringBlocks: [
          { required: true, message: 'Por favor, introduzca un monitoringBlocks', trigger: 'blur' }
        ],
        monitoringTimeMs: [
          { required: true, message: 'Por favor, introduzca un monitoringTimeMs', trigger: 'blur' }
        ],
        monitoringDelayMs: [
          { required: true, message: 'Por favor, introduzca un monitoringDelayMs', trigger: 'blur' }
        ],
        monitoringTimeoutSec: [
          { required: true, message: 'Por favor, introduzca un monitoringTimeoutSec', trigger: 'blur' }
        ]
      },
      fullscreenLoading: false
    }
  },
  created () {
    this.getDevice()
  },
  methods: {
    getDevice () {
      axios.get(this.$url + '/devices/' + this.$route.params.id).then(response => {
        this.deviceForm = response.data
      })
    },
    updateDevice (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.fullscreenLoading = true
          axios.put(this.$url + '/devices/' + this.deviceForm.id, JSON.stringify(this.deviceForm))
            .then(response => {
              this.fullscreenLoading = false
              this.$router.push({name: 'devices'})
              this.$notify({
                title: 'Success',
                message: this.$t('messagesBox.deviceUpdated', [this.deviceForm.id]),
                type: 'success'
              })
            })
            .catch(error => {
              this.fullscreenLoading = false
              console.log(error)
              this.$notify({
                title: 'Error',
                message: this.$t('messagesBox.deviceNotUpdate', [this.deviceForm.id]),
                type: 'error'
              })
            })
        } else {
          console.log('error submit!!')
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.incorrectForm'),
            type: 'warning'
          })
          return false
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 55%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.container-button {

  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;

}
.top-header {
  display: flex;
  align-items: center;
  width: 55%;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h2 {
  font-size: 16px;
  text-transform: capitalize;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-top: -40px;
  margin-left: 40px;
}
.select {
  margin-top: 22px;
  margin-bottom: 39px;
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.button-right {
  margin-bottom: 20px;
  margin-top: 20px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: right;
  position: relative;
  width: auto;
  height: 100%;
}
.border-bottom {
  border-bottom: solid;
  border-width: 1px;
  border-color:#D8D8D8;
}
.padding-top-bottom {
  padding-bottom: 40px;
  padding-top: 30px;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
</style>



