export const ChartData = {
  type: 'bar',
  options: {
    responsive: true,
    lineTension: 1,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true,
          padding: 0
        }
      }]
    }
  }
}
export default ChartData



