<template>
  <div>
    <ods-row type="flex" justify="space-between" class="top-buttons">
      <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8" class="margin-left">
        <ods-row>
          <ods-col class="select" :xs="24" :sm="20" :md="20" :lg="13" :xl="9">
            <h3 class="select-text">{{$t('commandForm.selectSignal')}}:</h3>
            <ods-select v-model="signalId" v-on:change="getCommands">
              <ods-option
                v-for="item in signalList"
                :key="item.id"
                :label="item.id"
                :value="item.id">
              </ods-option>
            </ods-select>
          </ods-col>
        </ods-row>
      </ods-col>
      <ods-col class="button-header">
        <h3 class="h3">{{$t('button.newCommand')}}</h3>
        <ods-button icon="ods-icon-plus" circle size="small" v-on:click="toNewCommand"></ods-button>
      </ods-col>
    </ods-row>
    <ods-row type="flex" class="top-header">
      <ods-col class="button-left" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
      <ods-col :span="24">
        <h1 class="title">{{$t('commandForm.title', [$route.params.id])}}</h1>
      </ods-col>
    </ods-row>
    <ods-row type="flex" justify="center" class="container">
      <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" >
        <ods-table stripe :data="commands" :default-sort = "{prop: 'id', order: 'ascending'}"  size=mini>
          <ods-table-column
            prop="id"
            sortable
            :label="$t('signalForm.id')">
          </ods-table-column>
          <ods-table-column
            prop="description"
            :label="$t('signalForm.description')">
          </ods-table-column>
          <ods-table-column
            prop="value"
            :label="$t('signalForm.value')">
          </ods-table-column>
          <ods-table-column
            fixed="right"
            :label="$t('signalForm.operation')"
            width="200">
            <template slot-scope="scope">
              <ods-button class="height" icon="ods-icon-hand" type="text" size="mini" v-on:click="executeCommand(scope.row)" v-loading.fullscreen.lock="fullscreenLoading"></ods-button>
              <ods-button class="height" icon="ods-icon-edit" type="text" size="mini" v-on:click="toUpdateCommand(scope.row)" ></ods-button>
              <ods-button class="height" icon="ods-icon-delete" type="text" size="mini" v-on:click="showDialogDelete(scope.row)"></ods-button>
            </template>
          </ods-table-column>
        </ods-table>
      </ods-col>
    </ods-row>
    <ods-dialog :title="$t('deleteDialogCommand.title')" :visible.sync="dialogVisible" width="30%">
      <span>{{$t('deleteDialogCommand.message')}}</span>
      <span slot="footer" class="dialog-footer">
        <ods-button type="text" @click="dialogVisible = false">{{$t('button.cancel')}}</ods-button>
        <ods-button type="primary" @click="deleteCommand()">{{$t('button.confirm')}}</ods-button>
      </span>
    </ods-dialog>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'Commands',
  data () {
    return {
      signalList: [],
      signalId: '',
      commandId: '',
      commands: [],
      fullscreenLoading: false,
      dialogVisible: false
    }
  },
  created () {
    this.getSignals()
  },
  methods: {
    getSignals () {
      axios.get(this.$url + '/devices/' + this.$route.params.id + '/signals/commands').then(response => {
        this.signalList = response.data
        if (this.signalList === null) {
          this.$notify({
            title: 'Error',
            message: this.$t('messagesBox.error'),
            type: 'error'
          })
        } else if (this.signalList === 'NODATA') {
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.noCommands'),
            type: 'warning'
          })
        }
      })
    },
    getCommands () {
      this.commands = []
      var i
      for (i = 0; i < this.signalList.length; i++) {
        if (this.signalList[i].id === this.signalId) {
          this.commands = this.signalList[i].commands
          break
        }
      }
    },
    executeCommand (row) {
      this.fullscreenLoading = true
      axios.put(this.$url + '/devices/' + this.$route.params.id + '/signals/' + this.signalId + '/commands/' + row.id + '/execute')
        .then(response => {
          this.fullscreenLoading = false
          this.$notify({
            title: 'Success',
            message: this.$t('messagesBox.execute'),
            type: 'success'
          })
        })
        .catch(error => {
          this.fullscreenLoading = false
          console.log(JSON.stringify(error.response.data))
          this.$notify({
            title: 'Error',
            message: this.$t('messagesBox.notExecute'),
            type: 'error'
          })
        })
    },
    toNewCommand () {
      this.$router.push({ name: 'newCommand', params: { id: this.$route.params.id } })
    },
    toUpdateCommand (row) {
      this.$router.push({ name: 'updateCommand', params: { id: this.$route.params.id, signalId: this.signalId, commandId: row.id } })
    },
    showDialogDelete (row) {
      this.dialogVisible = true
      this.commandId = row.id
    },
    deleteCommand () {
      axios.delete(this.$url + '/devices/' + this.$route.params.id + '/signals/' + this.signalId + '/commands/' + this.commandId)
        .then(response => {
          this.dialogVisible = false
          this.$router.push({name: 'devices'})
          this.$notify({
            title: 'Success',
            message: 'Command ' + this.commandId + ' deleted',
            type: 'success'
          })
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 70%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.title {
  font-size: 22px;
  color: #060E14;
  margin: 0 0 35px 0;
}
.select {
  display: flex;
  color: #888A91;
  font-size: 12px;
}
.select-text {
  margin-top: 10px;
  width: 100%;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
}
.top-header {
  display: flex;
  align-items: center;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
.button-header {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
  margin-right: 24px;
}
.margin-left {
  margin-left: 24px;
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.height {
  height: 30px;;
}
</style>



