<template>
  <div>
    <ods-row type="flex" justify="end" class="top-header">
      <ods-col class="button-right" :span="24">
        <h3 class="h3">{{$t('button.newDevice')}}</h3>
        <ods-button icon="ods-icon-plus" circle size="small" v-on:click="toNewDevice"></ods-button>
      </ods-col>
    </ods-row>
    <ods-row :gutter="16" class="container">
      <ods-col :xs="24" :sm="12" :md="8" :lg="8" :xl="6" v-for="device in devicesList" :key="device.id" >
        <device-card v-bind:device="device" v-on:refreshDevices='getDevices'></device-card>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>
import DeviceCard from '@/components/DeviceCard'

import axios from 'axios'

export default {
  name: 'Devices',
  components: {
    DeviceCard
  },
  data () {
    return {
      devicesList: []
    }
  },
  created () {
    this.getDevices()
  },
  methods: {
    getDevices () {
      axios.get(this.$url + '/devices').then(response => {
        this.devicesList = response.data
      })
    },
    toNewDevice () {
      this.$router.push({name: 'newDevice'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  // margin: 16px;
  padding:16px 16px 16px 16px;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
}
.top-header {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
.button-right {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
  margin-right: 24px;
}
</style>



