<template>
    <ods-menu
        class="ods-menu-vertical-demo"
        :collapse="isCollapse"
        menuTrigger="click"
        position="fixed"
        :isFirstLevel="true">
        <li class="ods-menu--logo" @click="isCollapse = !isCollapse">
            <ods-icon class="ods-menu--logo__icon" name="logo"></ods-icon>
        </li>
        <ods-menu-item index="1" v-on:click="toDashboard">
            <i class="ods-icon-dashboard"></i>
            <span slot="title">{{$t('menu.dashboard')}}</span>
        </ods-menu-item>
        <ods-menu-item index="2" v-on:click="toDevices">
            <i class="ods-icon-broadcast"></i>
            <span slot="title">{{$t('menu.devices')}}</span>
        </ods-menu-item>
        <ods-menu-item index="3" v-on:click="toMonitor">
            <i class="icon-ico_monitoreo"></i>
            <span slot="title">{{$t('menu.monitor')}}</span>
        </ods-menu-item>
        <ods-menu-item index="5" @click="change('1880')">
            <i class="icon-ico_nodered"></i>
            <span slot="title" >{{$t('menu.nodered')}}</span>
        </ods-menu-item>
        <ods-menu-item index="6" @click="change('4200')">
            <i class="icon-ico_terminal"></i>
            <span slot="title" >{{$t('menu.shell')}}</span>
        </ods-menu-item>
    </ods-menu>
</template>

<script>

export default {
  name: 'sideNavigation',
  data () {
    return {
      isCollapse: true
    }
  },
  methods: {
    toDevices () {
      this.$router.push({name: 'devices'})
    },
    toDashboard () {
      this.$router.push({name: 'dashBoardChart'})
    },
    toMonitor () {
      this.$router.push({name: 'monitor'})
    },
    change (port) {
      let app = window.open('https://' + window.location.hostname + ':' + port)
      app.focus()
    }
  }
}
</script>

<style lang="scss" scoped>
.red {
  color: red !important;
}
</style>



