<template>
  <div>
    <ods-module class="card" shadow="hover">
      <div slot="header" class="clearfix">
        <ods-row class="border">
          <ods-col :xs="23" :sm="23" :md="23" :lg="23" :xl="23">
            <h1>{{device.id}}</h1>
          </ods-col>
          <ods-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1">
            <ods-dropdown @command="handleCommand" trigger="click">
              <span class="ods-dropdown-link">
                <i class="ods-icon-overflow-menu"></i>
              </span>
              <ods-dropdown-menu slot="dropdown">
                <ods-dropdown-item command="edit"><ods-icon class="icon margin-right-15" name="edit"/>{{$t('dropDownDevice.edit')}}</ods-dropdown-item>
                <ods-dropdown-item command="delete"><ods-icon class="icon margin-right-15" name="delete"/>{{$t('dropDownDevice.delete')}}</ods-dropdown-item>
                <ods-dropdown-item command="signals"><i class="icon margin-right-15 icon-icon_senales"></i>{{$t('dropDownDevice.signals')}}</ods-dropdown-item>
                <!-- <ods-dropdown-item command="commands"><ods-icon class="icon margin-right-15" name="configure" disabled />{{$t('dropDownDevice.commands')}}</ods-dropdown-item> -->
              </ods-dropdown-menu>
            </ods-dropdown>
          </ods-col>
        </ods-row>
      </div>
      <ods-row class="margin-bottom-40">
        <ods-col class="image" :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
          <i class="icon-big icon-ico_new_ethernet" v-if="device.protocolType.toUpperCase() == 'TCP'"/>
          <i class="icon-big icon-ico_puerto_serie" v-if="device.protocolType.toUpperCase() == 'RTU'"/>
        </ods-col>
        <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
          <ods-row>
            <ods-col class="cardData">
              <h2 class="title" v-if="device.protocolType.toUpperCase() == 'TCP'">{{$t('cardData.ip')}}:</h2>
              <h2 class="value" v-if="device.protocolType.toUpperCase() == 'TCP'">{{device.ip}}</h2>
              <h2 class="title" v-if="device.protocolType.toUpperCase() == 'RTU'">{{$t('cardData.serial')}}:</h2>
              <h2 class="value" v-if="device.protocolType.toUpperCase() == 'RTU'">{{device.serial}}</h2>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-col class="cardData">
              <h2 class="title" v-if="device.protocolType.toUpperCase() == 'TCP'">{{$t('cardData.port')}}:</h2>
              <h2 class="value" v-if="device.protocolType.toUpperCase() == 'TCP'">{{device.port}}</h2>
              <h2 class="title" v-if="device.protocolType.toUpperCase() == 'RTU'">{{$t('cardData.bauds')}}:</h2>
              <h2 class="value" v-if="device.protocolType.toUpperCase() == 'RTU'">{{device.bauds}}</h2>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-col class="cardData">
              <h2 class="title">{{$t('cardData.manufacturer')}}:</h2>
              <h2 class="value">{{device.manufacturer}}</h2>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-col class="cardData">
              <h2 class="title">{{$t('cardData.model')}}:</h2>
              <h2 class="value">{{device.model}}</h2>
            </ods-col>
          </ods-row>
          <ods-row>
            <ods-col class="cardData" style="margin-top: 10px;">
              <div class="divDeviceType">
                <h2 class="deviceType">{{device.deviceType}}</h2>
              </div>
            </ods-col>
          </ods-row>
        </ods-col>
      </ods-row>
      <ods-row>
        <ods-col class="cardData" :offset="18">
          <div v-if="device.onError" class="divError">
            <h2 class="onError">{{onError}}</h2>
          </div>
          <div v-if="!device.onError" class="divActive">
            <h2 class="onError">{{onError}}</h2>
          </div>
        </ods-col>
      </ods-row>
    </ods-module>
    <ods-dialog :title="$t('deleteDialogDevice.title')" :visible.sync="dialogVisible" width="30%">
      <span>{{$t('deleteDialogDevice.message')}}</span>
      <span slot="footer" class="dialog-footer">
        <ods-button type="text" @click="dialogVisible = false">{{$t('button.cancel')}}</ods-button>
        <ods-button type="primary" @click="deleteDevice(device)">{{$t('button.confirm')}}</ods-button>
      </span>
    </ods-dialog>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'DeviceCard',
  props: ['device'],
  data () {
    return {
      dialogVisible: false,
      onError: 'ACTIVE'
    }
  },
  created () {
    if (this.device.onError) {
      this.onError = 'ERROR'
    }
  },
  methods: {
    handleCommand (command) {
      if (command === 'edit') {
        this.toUpdateDevice()
      } else if (command === 'delete') {
        this.showDialogDelete()
      } else if (command === 'signals') {
        this.toSignals()
      } else if (command === 'commands') {
        this.toCommands()
      }
    },
    showDialogDelete () {
      this.dialogVisible = true
    },
    deleteDevice () {
      axios.delete(this.$url + '/devices/' + this.device.id)
        .then(response => {
          this.$emit('refreshDevices')
          this.dialogVisible = false
          this.$notify({
            title: 'Success',
            message: 'Device ' + this.device.id + ' deleted',
            type: 'success'
          })
        })
        .catch(error => {
          console.log(JSON.stringify(error.response.data))
        })
    },
    toUpdateDevice () {
      this.$router.push({ name: 'updateDevice', params: { id: this.device.id } })
    },
    toSignals () {
      this.$router.push({ name: 'signals', params: { id: this.device.id } })
    },
    toCommands () {
      this.$router.push({ name: 'commands', params: { id: this.device.id } })
    }
  }
}
</script>

<style lang="scss" scoped>
.border {
  padding-bottom: 10px;
  border-bottom: solid;
  border-width: 1px;
  border-color:#D8D8D8;
}
.card {
  margin-bottom: 16px;
  border-radius: 0px;
}
h1 {
  font-size: 14px;
}
h2 {
  margin-bottom: 5px;
}

.image {
width: 100px;
margin-right: 15px;
}

.icon {
  font-size: 16px;
  color: #6E767D;
}
.icon-big {
  font-size: 100px;
  color: #A5C0D3;
  margin-top: 5px;
}

.cardData {
  display: flex;
  font-size: 11px;
  line-height: 11px;
  text-transform: uppercase;
  width: 100%;
}
.title {
  color: black;
  margin-right: 5px;
}
.value {
  color: gray;
}
.deviceType {
  color: #2E6C99;
  font-size: 9px;
  padding-top: 6px;
  padding-bottom: 3px;
  padding-left: 25px;
  padding-right: 25px;
}
.divDeviceType {
  border-radius: 6px;
  background-color: #E4EEF2;
}
.ods-dropdown-link {
  cursor: pointer;
}
.margin-right-15{
  margin-right: 15px;
}
.margin-bottom-40 {
  margin-bottom: 40px;
}
.divActive {
  border-radius: 6px;
  background-color: #79C5B4;
  padding-top: 6px;
  padding-bottom: 0px;
  padding-left: 25px;
  padding-right: 25px;
}
.divError {
  border-radius: 6px;
  background-color: #DE2F2F;
  padding-top: 6px;
  padding-bottom: 0px;
  padding-left: 25px;
  padding-right: 25px;
}
.onError {
  color: white;
  font-size: 9px;
}
</style>



